import React from 'react';
import logo from '../../assets/images/dhaka-eats-logo.png';
import mobilescreen from '../../assets/images/mobilescreen.png';
import screens from '../../assets/images/screens2.png';
import classes from './Siteleft.module.css';

const Siteleft = () => {
    return (
        <div className={classes.siteleft}>
            <a href="/">
                <img src={logo} alt="dhaka-eats-logo"/>
            </a>
            <h3 className={classes.mobileversion}>A Better way to find food</h3>

            <div className={[classes.contentheadding, 'd-none'].join(' ')}>
                <h2>Read reviews <span>from <br/> real people</span></h2>
                <h2>Search <span>for the best food<br/> around you</span></h2>
                <h2><span>Get the info you need<br/> the</span> restaurant</h2>
                <h2><span>Don't know</span> where to eat <br/><span>around Dhaka?</span><br/><br/><span>We've got you covered</span></h2>
            </div>
            <div className={classes.mobilescreen}>
                <img className={classes.frame} src={mobilescreen} alt="mobilescreen"/>
                <img className={classes.screens} src={screens} alt="screens"/>
            </div>
        </div>
    )
}

export default Siteleft;

