import React from 'react';
import classes from './Siteright.module.css';

const Siteright = () => {
    return (
        <div className={classes.siteright}>
            <h3 className="d-block">A Better way to find food</h3>
            <div className={[classes.contentheadding, 'd-block'].join(' ')}>
                <h2>Read reviews <span>from <br/> real people</span></h2>
                <h2>Search <span>for the best food<br/> around you</span></h2>
                <h2><span>Get the info you need<br/> the</span> restaurant</h2>
                <h2><span>Don't know</span> where to eat <br/><span>around Dhaka?</span><br/><br/><span>We've got you covered</span></h2>
            </div>
            <form action="#" className={classes.newslatter}>
                <h4>Signup for updates on the launch!</h4>
                <div className={classes.form}>
                    <input type="email" placeholder="Enter your email"/>
                    <button type="submit">Submit</button>
                </div>
            </form>
        </div>
    )
}

export default Siteright;

